import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components: {
		uniPopup
	},
	props: {
		orderId: {
			type: Number,
			default: 0,
		},
	},
	data() {
		return {
			payType: null
		}
	},
	methods: {
		cancelOrder() {
			this.$api.payment.closeOrder(this.orderId).then(data => {
				this.$emit("returnDate", data);
				uni.showToast({
					title: '订单已关闭',
					duration: 2000
				});
				uni.switchTab({
					url: '../order/order'
				})
			})
		},
		ChoseType(item) {
			this.$refs.popup.open();
		},
		changePayType(type) {
			this.payType = type;
		},
		confirm() {
			if (this.payType == 1) {
				this.$api.payment.payOrder(1, this.orderId).then(data => {
					console.log(data)
					this.$api.wePay(data.data);
					this.$refs.popup.close();		
				})
			} else if (this.payType == 2) {
				this.$api.payment.payOrder(2, this.orderId).then(data => {
					console.log(this.orderId)
					if (data.success) {
						// uni.showToast({
						// 	title: '支付成功',
						// 	duration: 2000
						// });
						this.$emit("returnDate", data);
						this.$refs.popup.close();
							uni.switchTab({
								url: '../order/order'
							})
						
					} else {
						this.$emit("returnDate", data);
						// uni.showToast({
						// 	title: '余额不足',
						// 	duration: 2000
						// });
						this.$refs.popup.close();
							uni.switchTab({
								url: '../order/order'
							})
				
					}
				})
			} else {
				this.$api.msg('请选择支付方式')
			}
		}
	}
}
